.money {
  margin-top: 0.2rem;
  background: #f7f4e2 url(./money.png) no-repeat;
  background-position: right top;
  background-size: auto 100%;
  width: 7.5rem;
  height: 1.32rem;
  position: relative; }
  .money .tip {
    font-size: 0.32rem;
    color: #c5b348;
    position: absolute;
    left: 2.4rem;
    top: 0.3rem;
    font-weight: bold;
    opacity: 0.7; }
  .money .big {
    font-size: 0.48rem;
    color: #ff9701; }

.zfb {
  position: absolute;
  left: 0.4rem;
  top: 0.2rem;
  font-size: 0.2rem;
  color: #03a9f4;
  cursor: pointer; }

.weixin {
  position: absolute;
  left: 0.4rem;
  top: 0.72rem;
  font-size: 0.2rem;
  color: #089e0e;
  cursor: pointer; }

.icon-weixin {
  margin: 0 0.07rem; }



