.Game {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.7);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center; }
  .Game .icon-guanbi {
    font-size: 30px;
    color: #999;
    position: absolute;
    right: 4px;
    top: 4px;
    z-index: 999;
    cursor: pointer; }

.iframe {
  border: none;
  margin: 0;
  padding: 0;
  background: #fff;
  /*animation: turnout 1 1s ease;*/ }

@-webkit-keyframes turnout {
  from {
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0); }
  to {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1); } }

@keyframes turnout {
  from {
    -webkit-transform: scale(0, 0);
    transform: scale(0, 0); }
  to {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1); } }



